import type { BiPlugin } from '@ali/4ever-cangjie';

export type EmbedCommonPluginConfig = {
  /**
   * 国际化文案配置
   */
  locale?: Record<string, string>;
  /**
   * 文件上传时调用
   * @param {File} file - 文件对象（原生的文件）
   * @param {string} id - 内部 id
   * @param {Function} notifyProgress - 上传进度回调（0-100）
   */
  uploadFile?: (
    file: File,
    id: string,
    notifyProgress: (percent: number) => void,
  ) => Promise<{ url: string }>;
  /**
   * 文件下载下载时调用，不传入的情况，不会显示下载按钮。
   * @param {string} src - 文件原始地址
   * @param {Object} file - 文档信息
   */
  downloadFile?: (
    src: string,
    file: { name: string; type: string; size: number },
  ) => void;
  /**
   * 文件是否支持预览，默认为false。
   */
  enableFilePreview?: boolean;
  /**
   * 是否允许下载，默认值为 true
   * @param {string} src - 文件原始地址
   */
  allowDownload?: ((src: string) => Promise<boolean>) | boolean;
  /**
   * 点击预览时调用，用于预览卡片模式的内容
   * @param {string} src - 文件原始地址
   */
  onPreview?: (src: string) => void;
};

export type EmbedFactoryParams = Pick<BiPlugin, 'commands' | 'toolbar' | 'menubar' | 'quickInsert'> & {
  configName: string;
  createRenderNode: (config: Record<string, any>) => BiPlugin['renderNode'];
  actions?: Record<string, any>;
};

export const UploadProgress = {
  BEGIN: 'begin',
  SUCCESS: 'success',
  FAIL: 'fail',
};
